const startBtn = document.querySelector("#keys-start");
const resetBtn = document.querySelector("#keys-reset");
const keysOutput = document.querySelector("#keys-output");
const scoreOutput = document.querySelector("#score-output");
const keys = [
  {
    name: "KeyA",
    letter: "a",
  },
  {
    name: "KeyR",
    letter: "r",
  },
  {
    name: "KeyF",
    letter: "f",
  },
  {
    name: "KeyC",
    letter: "c",
  },
  {
    name: "KeyL",
    letter: "l",
  },
  {
    name: "KeyS",
    letter: "s",
  },
  {
    name: "KeyM",
    letter: "m",
  },
  {
    name: "KeyP",
    letter: "p",
  },
  {
    name: "KeyX",
    letter: "x",
  },
  {
    name: "KeyH",
    letter: "h",
  },
];
let currentIndex = 0;
let currentKey = {};
let isPlaying = false;
let score = 0;

resetBtn.style.display = "none";

function getCurrentKey() {
  if (currentIndex >= keys.length) {
    currentIndex = 0;
  }
  const currentItem = keys[currentIndex];
  keysOutput.textContent = currentItem.letter;
  currentIndex += 1;
  return currentItem;
}

// function startGame() {
//   startBtn.style.display = "none";
//   resetBtn.style.display = "block";
// }

window.addEventListener("keydown", (e) => {
  if (!isPlaying) {
    return;
  }
  if (currentKey.name === e.code) {
    console.log("true");
    currentKey = getCurrentKey();
  } else {
    console.log("false");
  }
});

startBtn.addEventListener("click", () => {
  startBtn.style.display = "none";
  resetBtn.style.display = "block";
  isPlaying = true;
  currentKey = getCurrentKey();
});
